html {
	border:0;
	margin:0;
	padding:0;
}

body {
	border: 0;
	margin: 0;
	padding: 0;
	background-color: #e5e5e5;
	color: #333;
	font-family: "Roboto",UILanguageFont,Arial,sans-serif;
	font-size: 13px;
	font-weight: 300;
	line-height: 1.4;
	height: 100%;
	min-width: 1024px;
	position: relative;
	overflow-x: hidden;
}

header {
	height:48px;
	background-color:#fff;
}

.card {
	display: inline-block;
	margin: 5px;
	text-align: left;
	vertical-align: top;
	height: 288px;
	width: 126px;
}

.cover {
	
		width:100%;
	
}